@import "variables";
@import "colors";

.about {
	width: 60%;
	height: 100%;
	margin: auto;
	display: grid;
	align-content: center;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 200px fit-content;
	gap: 30px 30px;
	grid-template-areas:
		"ABOUT ABOUT ABOUT"
		"EL1 EL2 EL3";
	&_element {
		display: flex;
		flex-direction: column;
		background: rgba($color-grey, 0.8);
		padding: $about_el_pad;
		border-radius: $about_el_bord_rad;
		&_title {
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 28px;
			font-weight: 500;
			margin-bottom: 10px;
			span {
				height: fit-content;
			}
		}
		&_content {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			align-items: center;
			flex-grow: 1;
			ul {
				display: flex;
				justify-content: center;
				flex-wrap: wrap;
				list-style-type: none;
				li {
					margin: 5px 3px;
					width: fit-content;
					background: transparent;
					border-radius: 10px;
					border: 2px solid white;
					padding: 5px 10px;
					font-size: 16px;
					font-weight: 500;
					-webkit-transition: all 0.3s ease;
					-moz-transition: all 0.3s ease;
					-ms-transition: all 0.3s ease;
					-o-transition: all 0.3s ease;
					transition: all 0.3s ease;
				}
				li:hover {
					background: white;
					color: black;
				}
			}
		}
	}
	#aboutme {
		grid-area: ABOUT;
		.about_element_title {
			justify-content: flex-start;
		}
		.about_element_content {
			font-size: 18px;
			justify-content: flex-start;
		}
	}
	#frontend {
		grid-area: EL1;
	}
	#backend {
		grid-area: EL2;
	}
	#other {
		grid-area: EL3;
	}
}

@media (min-width: 1921px) {
}

@media (max-width: 1400px) {
	.about {
		width: 70%;
		&_element {
			&_title {
				font-size: 24px;
			}
			&_content {
				ul {
					li {
						font-size: 14px;
					}
				}
			}
		}
	}
}

@media (max-width: 1200px) {
	.about {
		width: 80%;
	}
}

@media (max-width: 992px) {
	.about {
		max-height: 80%;
		grid-template-columns: 1fr;
		grid-template-rows: fit-content fit-content fit-content fit-content;
		gap: 20px 0;
		grid-template-areas:
			"ABOUT"
			"EL1"
			"EL2"
			"EL3";
	}
}

@media (max-width: 768px) {
}

@media (max-width: 576px) {
	.about {
		width: 80%;
		&_element {
			padding: $about_el_pad_mobile;
			&_title {
				height: fit-content;
				font-size: 18px;
			}
			&_content {
				height: fit-content;
				ul {
					li {
						font-size: 10px;
						padding: 3px 6px;
						margin: 3px 2px;
						border: 1px solid white;
						border-radius: 8px;
					}
				}
			}
		}
		#aboutme {
			.about_element_title {
				justify-content: center;
			}
			.about_element_content {
				justify-content: center;
				font-size: 14px;
			}
		}
	}
}

@media (max-width: 320px) {
	.about {
		width: 90%;
		&_element {
			padding: 10px;
		}
	}
}
